import Apis from "../assets/apis";
import axios from "axios";
export default {
  components: {},
  props: ["stateColombia", "action"],
  data() {
    return {
      varprueba: "desde front",
      res: true,
      status: false,
      states: [],
      citys: [],
      respStates: {},
      // variables del formulario
      name: "",
      email: "",
      state: "",
      city: "",
      // end variables
    };
  },
  async created() {
    console.log("antes de montar");
    try {
      const response = await axios.get(Apis.URL.URL_COLOMBIA);

      this.respStates = response.data;
      Object.keys(response.data).map((item) => this.states.push(item));
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
    async sendVisitor(e) {
      e.preventDefault();

      this.res = false;

      try {
        var response = await axios.post(Apis.baseURL + Apis.URL.URL_CONTACT, {
          name: this.name,
          email: this.email,
          state: this.state,
          city: this.city,
        });
        setTimeout(() => {
          $("#success").toast("show");
          this.action(response.data.message);
          this.res = true;
          this.name = "";
          this.email = "";
          this.state = "";
          this.city = "";
        }, 3000);
        console.log(response);
      } catch (error) {
        console.log(error);
      }
    },
    SelectSite(e) {
      this.citys = this.respStates[e.target.value];
    },
  },
  mounted() {},
};
